.profile {
  display: block;
}
.description {
  display: block;
  margin: 20px auto;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}
.avatar {
  border-radius: 50%;
  justify-content: center;
}
.stats {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px;
  list-style: none;
}
.statsItem {
  display: block;
  padding: 20px;
  list-style: none;
  color: gray;
  background-color: lavender;
}
